import {Box} from "./box";
import {Icon} from "../button/icon";
import {Label} from "../widget/label";
import {EventEmitter} from "../core/event-emitter";
import {EMouseEvent} from "../component/enum/e-mouse-event";
import jquery = require("jquery");

export interface IConfigAccordionItem{
	title?:string;
	colorIcon?:string;
	collapseIcon?:string;
	expandIcon?:string;
	disabled?:boolean;
	iconPosition?:"left"|"right";
	titleColor?:string;
	titleRender?:string;
	animationSpeed?:number|string;
	isOpen?:boolean;
	originClick?:HTMLElement;
}

/**
 * Este container Ã© aclopado no {Accordion} de containers e contem um Form.
 * 
 * <img src="../../doc_assets/accordion-example.png">
 * 
 * @example
 * import {Accordion, Box} from "mvcomponents/container";
 * import {Label} from "mvcomponents/widget";
 * import {LoginForm} from "./login-form";
 *
 * let myAccordion: Accordion = new Accordion()
 *      .append(new LoginForm(),{title:"teste"})
 *      .append(new Box(),{"title":"Titulo Accordion B"});
 * 
 * @export
 * @memberof module:mvcomponents/container
 * @extends {Box}
 */
export class AccordionItem extends Box{
	public header:Box;
	public body:Box;
	public config:IConfigAccordionItem;
	public onCollapse:EventEmitter<IConfigAccordionItem> = new EventEmitter();
	public onExpand:EventEmitter<IConfigAccordionItem> = new EventEmitter();
	private iconTitle:Icon;
	constructor(config:IConfigAccordionItem){
		super();
		this.config = config;
		this
			.setSize(12)
			.addStyleName("accordion-item");

		let iconTltStyle = this.config.iconPosition && this.config.iconPosition === "left" ? "left":"right";	
		this.iconTitle = new Icon()
			.addStyleName(`pull-${iconTltStyle}`)
			.setIcon(config.expandIcon)
			.setColor(config.colorIcon?config.colorIcon:"");

		if(this.config.disabled){
			this.setCssProperties({opacity:0.6});
		}

		this.header = new Box()			
			.addStyleName("accordion-header");

		if(config.titleRender){
			jquery(this.header.element).append(config.titleRender);
		}else{
			this.header
				.append(new Label(config.title||"").setCssProperties({color:this.config.titleColor}).setSize(11))
				.setCssProperties({color:this.config.titleColor});
		}

		if(this.config.iconPosition && this.config.iconPosition === "right"){
			this.header.append(this.iconTitle);
		}else{
			this.header.prepend(this.iconTitle);
		}	

		this.append(this.header);

		this.body = new Box()
			.setSize(12)
			.addStyleName("accordion-body");

		this.append(this.body);

		//this.iconTitle.addEvent(EMouseEvent.CLICK,() => this.toogleHeight());
		this.header.addEvent(EMouseEvent.CLICK,evt => this.toogleHeight(evt));
	}
	private toogleHeight(evt:Event){
		//console.log(evt.target);
		this.config.originClick = <HTMLElement>evt.target;
		let tmpConfig:IConfigAccordionItem = this.config;
		if(!this.config.disabled){
			if(this.iconTitle.hasStyleName(this.config.collapseIcon)){
				this.onExpand
						.emit(tmpConfig);
			}else{
				this.onCollapse
						.emit(tmpConfig);
			}
		}		
	}
	public expand():this{
		let tmpConfig:IConfigAccordionItem = this.config;
		this.iconTitle
			.removeStyleName(tmpConfig.collapseIcon)
			.addStyleName(tmpConfig.expandIcon);
		jquery(this.body.element).slideDown(tmpConfig.animationSpeed);
		return this;
	}
	public collapse():this{
		let tmpConfig:IConfigAccordionItem = this.config;		
		this.iconTitle
			.removeStyleName(tmpConfig.expandIcon)
			.addStyleName(tmpConfig.collapseIcon);
		jquery(this.body.element).slideUp(tmpConfig.animationSpeed);
		return this;
	}
}
